var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("红色龙卷风")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        红色龙卷风由作家Gardner Fox和艺术家Dick Dillin 创作，有感知的机器人Red Tornando首次出现在美国正义联盟＃64（1968年8月）。这个名字以前曾被用于滑稽的超级英雄Ma Hunkel，他于1939年在全美漫画中被引入并于1940年开始使用Red Tornado这个名字。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        1968年的红色龙卷风是一个有知觉的机器人，能够产生龙卷风速度风，使其能够飞行并执行其他与风相关的壮举。最初是美国司法协会的成员，它转移到另一个方面，加入了美国正义联盟。红色龙卷风的尸体被破坏或毁坏了。它于1981年被重新调整，用Ma Hunkel的记忆简单编程（而不是有感觉），直到一个年长的外星人supervillain（龙卷风冠军）栖息在机器人体内。在1984年的侦察中，龙卷风冠军从未居住过机器人身体。相反，元素空气占据了机器人。2011年重新启动 DC漫画世界，新52，从DC漫画中消除了红色龙卷风（虽然其他角色暗示其存在）。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Red Tornado由两个实体合并而成：一个由supervillain T. O. Morrow创建的机器人身体和来自Earth-One planet Rann的龙卷风冠军。龙卷风冠军是兰恩一个有感觉的龙卷风的道德良好的一部分。被称为Ulthoon，Rann的龙卷风暴君，这被Adam Strange击败并“考虑善恶的本质，并认为善是优势力”。Ulthoon意识到Strange与美国正义联盟（JLA）的冒险经历，并在1963年的一个故事中决定转移到一个无人居住的地球复制品。Ulthoon采取正义联盟的形式。它邪恶的一面，后来被称为龙卷风暴君，出现并击败了Ulthoon-Justice League。决定了解它的失败，龙卷风冠军去了地球（龙卷风暴君再次出现并击败真正的正义联盟）。在第二次攻击中，正义联盟将龙卷风暴君驱逐到一个“反物质宇宙”并且（他们相信）被摧毁。龙卷风冠军以重复的地球和JLA结束他的实验，并决定继续成为英雄。这个故事在美国正义联盟中重新出现卷。1＃193，龙卷风冠军告诉JLA成员Firestorm，它在1963年独自前往地球“重建”其与龙卷风暴君的失败之战。在学会如何击败其邪恶的一半之后，它又回到了复制品地球，击败了它的邪恶的一半，并将其“永远”放逐。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)]